
.btn_choose {
  width: 25px;
  height: 25px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 15px;
  border-radius: 20%;
  background-size: 25px 25px;
}
.select {
  background-image: url(../../img/select.png);
}
.unselect {
  background-image: url(../../img/unselect.png);
}
.list_container {
  position: relative;
}
.segment {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
.segment_control {
  border: none;
  background-color: #fff;
}
.von-button-bar .button-small {
  font-size: 18px;
  height: 50px;
  padding-top: 11px;
  border-radius: 0;
}
.von-button-bar {
  margin: 0 0;
}
.von-button-bar > .button:first-child {
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
}
.von-button-bar > .button:last-child {
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
}
.device_toggle {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}



